import React, { useEffect, useState } from 'react'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import * as actions from '../../store/actions'
import Avatar from 'antd/es/avatar'
import Card from 'antd/es/card'

import friendActionButton from '../../components/FriendActionButton'
import FriendCard from '../../components/FriendCard'
import Tabs from 'antd/es/tabs'
import Progresses from '../Progresses/Progresses'
import Wallet from '../Shop/Wallet'
import PerkCard from '../Shop/PerkCard'
import getAvatar from '../../utils/getAvatar'
import pullData from '../../utils/pullData'
import UserButton from '../../components/UserButton/UserButton'
import MasonryGrid from '../../components/MasonryGrid'
import Button from 'antd/es/button'
import Modal from 'antd/es/modal/Modal'
import MasonryCard from '../../components/MasonryCard'
import ProgressButton from '../../components/ProgressButton'
import FollowButton from '../../components/FollowButton'
import Tag from 'antd/es/tag'
import * as wsActions from '../../websocketActions'
import Upload from 'antd/es/upload'
import { uploadFile } from '../../utils/uploadFile'
import LoadingOutlined from '@ant-design/icons/LoadingOutlined'
import PlusOutlined from '@ant-design/icons/PlusOutlined'
import * as classes from './Profile.module.css'
import Typography from 'antd/es/typography'
import Input from 'antd/es/input'
import message from 'antd/es/message'
import Wishlist from '../Wishlist/Wishlist'
import Friends from '../Friends/Friends'
import LogoutOutlined from '@ant-design/icons/LogoutOutlined'
import WishlistAndPerks from '../Shop/WishlistAndPerks'
import Transactions from '../Shop/Transactions'

const Profile = props => {
    const [data, setData] = useState({})
    const [followersVisible, setFollowersVisible] = useState(false)
    const [followingVisible, setFollowingVisible] = useState(false)
    const { profile, friendData, account } = props.isAuthed ? props : data
    const [deleteVisible, setDeleteVisible] = useState()
    const [deleteField, setDeleteField] = useState('')

    useEffect(() => {
        const pathParts = props.history.location.pathname.split('/')
        if (pathParts.length > 2) {
            const profileId = pathParts[2]
            if (props.isAuthed) {
                props.setData({ currentProfile: profileId })
            } else {
                pullData(props, data, setData, { forceAuth: false }, data => {
                    const friendData = {}
                    for (let friend of data.friendData)
                        friendData[friend._id] = friend
                    const progressData = {}
                    for (let progress of data.progressData)
                        progressData[progress._id] = progress

                    props.setData({
                        // profile: data.profile,
                        friendData,
                        progressData,
                        account: {},
                    })
                })
            }
        } else {
            if (props.isAuthed) {
                props.setData({ currentProfile: account._id })
            }
        }
    }, [props.isAuthed, props.history.location.pathname, props.account._id])

    if (!profile || !profile._id) return null

    const isOwner = account._id && account._id === profile._id
    props.setPageTitle({ main: profile.name, sub: '' })

    const realFriends = [],
        invitedFriends = [],
        invitingFriends = []
    if (profile && profile.friends && account && account._id) {
        for (let friend of account.friends) {
            if (friend.status === 'friend') realFriends.push(friend)
            else if (friend.status === 'invited') invitedFriends.push(friend)
            else if (friend.status === 'inviting') invitingFriends.push(friend)
        }
    }
    const friendActions = friendActionButton({
        account,
        realFriends: realFriends,
        invitedFriends: invitedFriends,
        invitingFriends: invitingFriends,
        _id: profile._id,
        additionalButton: null,
        asText: true,
    })
    const loading = false

    return (
        <>
            <div>
                <Card>
                    <div style={{ textAlign: 'center', marginBottom: '50px' }}>
                        {isOwner ? (
                            <Upload
                                name="avatar"
                                listType="picture-card"
                                className={classes.Avatar}
                                showUploadList={false}
                                customRequest={({ file }) => {
                                    uploadFile(file, 800, 'avatar', () => {
                                        props.increaseAvatarV()
                                    })
                                }}
                            >
                                {account.image ? (
                                    <Avatar
                                        src={getAvatar(
                                            account._id,
                                            account.image
                                        )}
                                        size={150}
                                        style={{ margin: '25px' }}
                                    />
                                ) : (
                                    <div>
                                        {loading ? (
                                            <LoadingOutlined />
                                        ) : (
                                            <PlusOutlined />
                                        )}
                                        <div className="ant-upload-text">
                                            Upload
                                        </div>
                                    </div>
                                )}
                            </Upload>
                        ) : (
                            <Avatar
                                src={getAvatar(profile._id, profile.image)}
                                size={200}
                            />
                        )}
                        <Typography.Title
                            level={2}
                            editable={{
                                onChange: res =>
                                    wsActions.editAccount(
                                        { name: res },
                                        account._id
                                    ),
                            }}
                        >
                            {profile.name}
                        </Typography.Title>
                        {/* <h1 style={{ margin: 0 }}>{profile.name}</h1> */}
                        <h3>({profile._id})</h3>
                        <Button
                            type="text"
                            onClick={() => setFollowersVisible(true)}
                        >
                            {profile.followingAccounts.length} Followers
                        </Button>
                        <Button
                            type="text"
                            onClick={() => setFollowingVisible(true)}
                        >
                            {profile.followProgresses.length +
                                profile.followAccounts.length}{' '}
                            Following
                        </Button>
                        {isOwner && (
                            <div>
                                <Button
                                    icon={<LogoutOutlined />}
                                    onClick={() =>
                                        props.history.push('/logout')
                                    }
                                    danger
                                    type="text"
                                >
                                    Logout
                                </Button>
                            </div>
                        )}
                        <div>
                            {account && account._id !== profile._id && (
                                <span style={{ marginRight: '10px' }}>
                                    {realFriends.find(
                                        item => item.friend === profile._id
                                    ) ? (
                                        <Tag color="green">Friends</Tag>
                                    ) : null}
                                    {account.followAccounts.includes(
                                        profile._id
                                    ) ? (
                                        <Tag
                                            style={{ marginRight: '10px' }}
                                            color="purple"
                                        >
                                            Following
                                        </Tag>
                                    ) : (
                                        <Button
                                            type="primary"
                                            onClick={() =>
                                                wsActions.followAccount(
                                                    account._id,
                                                    profile._id
                                                )
                                            }
                                        >
                                            Follow
                                        </Button>
                                    )}
                                </span>
                            )}
                            {friendActions}
                        </div>
                    </div>
                    <Tabs>
                        <Tabs.TabPane tab="Progresses" key={0}>
                            <Progresses isOwner={isOwner} />
                        </Tabs.TabPane>

                        <Tabs.TabPane tab="Friends" key={1}>
                            {isOwner ? (
                                <Friends />
                            ) : (
                                <MasonryGrid friends={true}>
                                    {profile.friends
                                        .filter(
                                            friend => friend.status === 'friend'
                                        )
                                        .map(friend => {
                                            const oneFriendData = {
                                                ...friendData[friend.friend],
                                                _id: friend.friend,
                                            }
                                            return (
                                                <FriendCard
                                                    key={oneFriendData._id}
                                                    {...props}
                                                    account={account}
                                                    friend={oneFriendData}
                                                    realFriends={realFriends}
                                                    invitingFriends={
                                                        invitingFriends
                                                    }
                                                    invitedFriends={
                                                        invitedFriends
                                                    }
                                                />
                                            )
                                        })}
                                </MasonryGrid>
                            )}
                        </Tabs.TabPane>
                        {isOwner && (
                            <Tabs.TabPane tab="Shop" key={2}>
                                <WishlistAndPerks
                                    mode="shop"
                                    listKey="perks"
                                    account={props.account}
                                />
                            </Tabs.TabPane>
                        )}
                        {isOwner && (
                            <Tabs.TabPane tab="Wallet" key={3}>
                                <Wallet />
                            </Tabs.TabPane>
                        )}
                        {isOwner && (
                            <Tabs.TabPane tab="Transactions" key={4}>
                                <Transactions />
                            </Tabs.TabPane>
                        )}

                        {!isOwner && (
                            <Tabs.TabPane tab="Shop" key={5}>
                                <MasonryGrid>
                                    {profile.perks &&
                                        profile.perks.map((perk, index) => (
                                            <PerkCard
                                                key={index}
                                                perk={perk}
                                                perkId={perk.perkId}
                                                owner={profile}
                                                account={account || {}}
                                                onlyBuy={true}
                                                mode="shop"
                                            />
                                        ))}
                                </MasonryGrid>
                            </Tabs.TabPane>
                        )}
                        {!isOwner && (
                            <Tabs.TabPane tab="Wallet" key={6}>
                                <Wallet
                                    pulledProfile={profile}
                                    account={profile}
                                />
                            </Tabs.TabPane>
                        )}
                        <Tabs.TabPane tab="Wishlist" key={7}>
                            {isOwner ? (
                                <Wishlist />
                            ) : (
                                <MasonryGrid>
                                    {profile.wishlist.map((item, index) => (
                                        <PerkCard
                                            key={index}
                                            perk={item}
                                            perkId={item.itemId}
                                            owner={profile}
                                            account={account || {}}
                                            setEditedPerk={() => {}}
                                            setEditPerkOpen={() => {}}
                                            onlyBuy={true}
                                            mode="wishlist"
                                        />
                                    ))}
                                </MasonryGrid>
                            )}
                        </Tabs.TabPane>
                        {isOwner && (
                            <Tabs.TabPane tab="Settings" key={8}>
                                <Button
                                    danger={true}
                                    onClick={() => setDeleteVisible(true)}
                                >
                                    Delete account
                                </Button>
                                <Modal
                                    title="Delete account"
                                    visible={deleteVisible}
                                    onOk={() => {
                                        if (deleteField !== account._id) {
                                            message.warning(
                                                'The nickmanme is not correct.'
                                            )
                                        } else {
                                            wsActions.deleteAccount(account._id)
                                        }
                                    }}
                                    onCancel={() => {
                                        setDeleteVisible(false)
                                        setDeleteField('')
                                    }}
                                >
                                    <p>
                                        Please enter your nickname and press Ok
                                        to delete your account.
                                    </p>
                                    <Input
                                        onChange={e =>
                                            setDeleteField(e.target.value)
                                        }
                                    />
                                </Modal>
                            </Tabs.TabPane>
                        )}
                    </Tabs>
                </Card>
            </div>
            <Modal
                title="Followers"
                visible={followersVisible}
                footer={null}
                onCancel={() => setFollowersVisible(false)}
            >
                {profile.followingAccounts.map(item => (
                    <MasonryCard
                        noLink={true}
                        key={item}
                        actions={<FollowButton account={account} id={item} />}
                        body={<UserButton span={true} id={item} size={50} />}
                    />
                ))}
            </Modal>
            <Modal
                title="Following"
                visible={followingVisible}
                footer={null}
                onCancel={() => setFollowingVisible(false)}
            >
                <Tabs>
                    <Tabs.TabPane tab="People" key="1">
                        {profile.followAccounts.map(item => (
                            <MasonryCard
                                noLink={true}
                                key={item}
                                actions={
                                    <FollowButton account={account} id={item} />
                                }
                                body={
                                    <UserButton
                                        span={true}
                                        id={item}
                                        size={50}
                                    />
                                }
                            />
                        ))}
                    </Tabs.TabPane>
                    <Tabs.TabPane tab="Inspirations" key={2}>
                        {profile.followProgresses.map(item => (
                            <MasonryCard
                                noLink={true}
                                key={item}
                                actions={
                                    <FollowButton
                                        type="progress"
                                        account={account}
                                        id={item}
                                    />
                                }
                                body={
                                    <ProgressButton
                                        span={true}
                                        id={item}
                                        size={50}
                                    />
                                }
                            />
                        ))}
                    </Tabs.TabPane>
                </Tabs>
            </Modal>
        </>
    )
}

const mapStateToProps = state => {
    return {
        account: state.account,
        profile: state.profile,
        isAuthed: state.isAuthed,
        friendData: state.friendData,
    }
}

const mapDispatchToProps = (dispatch: any) => {
    return {
        setData: data => dispatch(actions.setData(data)),
        setPageTitle: title => dispatch(actions.setPageTitle(title)),
        tryAuth: (props, forceAuth) =>
            dispatch(actions.tryAuth(props, forceAuth)),
        increaseAvatarV: () => dispatch(actions.increaseAvatarV()),
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        mapDispatchToProps
    )(Profile)
)
